import React, { useEffect, useCallback, useState, useRef } from "react";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import queryString from "query-string";

import "./styles.scss";

const LazyLoadImageWithDimensions = (props) => {
    const ref = useRef(null);

    const placeholderSrc = (img) => {
        let { width, height } = img;
        return `data:image/svg+xml,%3Csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 ${width} ${height}"%3E%3C/svg%3E`;
    };

    const [state, setState] = useState({
        loaded: false,
        src: placeholderSrc(props.img),
        shouldFetch: true
    });

    const loadImage = useCallback (() => {
        setState({
            ...state,
            src: props.img.url,
            shouldFetch: false,
        });
    }, [props.img.url, state]);

    const shouldLoadImage = useCallback((componentOffsetTop, userYPosition) => {
        return state.shouldFetch && !state.loaded && componentOffsetTop <= userYPosition + document.body.clientHeight + 100; 
    }, [state.loaded, state.shouldFetch]);

    const onImageLoad = () => {
        if (!state.loaded && state.src === props.img.url) {
            setState({
                ...state,
                loaded: true
            });
        }
    };

    const handleScroll = useCallback(() => {
        const yPosition = window.scrollY;
        if (shouldLoadImage(ref.current.offsetTop, yPosition)) {
            loadImage();
            window.removeEventListener("scroll", handleScroll);
        }
    }, [loadImage, shouldLoadImage]);

    useEffect(() => {
        const yPosition = window.scrollY;
        // Handles initial mount to check if image is already within threshold without scrolling.
        if (shouldLoadImage(ref.current.offsetTop, yPosition)) {
            loadImage();
        } else {
            window.addEventListener('scroll', handleScroll);
        }

        return () => {
            window.removeEventListener("scroll", handleScroll);
        };
    }, [shouldLoadImage, loadImage, handleScroll, state]);

    const { url, alt } = props.img;
    const { loaded, src } = state;
    let classes = !loaded ? "LazyLoadAnimation" : "";
    if (props.classNames) {
        classes += " " + props.classNames;
        classes = classes.trim();
    }

    return (
        <>
            <img ref={ref} onLoad={onImageLoad} className={classes} src={src} data-src={url} alt={alt} loading="lazy" />
            <noscript>
                <img src={url} alt={alt} loading="lazy" />
            </noscript>
        </>
    );
}

const Img = (props) => {
    const {src} = props;
    if (src.includes("imgur.com") && !src.includes(".jpg")) {
        const highResUrl = src + "h.jpg";
        const lowResUrl = src + "m.jpg";
        return (
            <div className="LazyLoadImageWrapper">
                <LazyLoadImage {...props} src={highResUrl} delayTime={300} placeholderSrc={lowResUrl} effect="blur" threshold={300} />
            </div>
        );
    } else {
        const sepIdx = src.indexOf("?")
        if (sepIdx >= 0) {
            const params = queryString.parse(src.substring(sepIdx));
            if (params.height && params.width) {
                const img = {
                    url: src,
                    height: params.height,
                    width: params.width,
                    alt: props.alt
                }
                return (
                    <div className="LazyLoadImageWrapper">
                        <LazyLoadImageWithDimensions img={img} {...props} />
                    </div>
                );
            }
        }
        return (
            <div className="LazyLoadImageWrapper">
                <LazyLoadImage {...props} effect="blur" threshold={300} />
            </div>
        );
    }
};

export default Img; 